

*,
*:after,
*:before {
    box-sizing: border-box;
}

input, button, textarea, select {
  font: inherit;
  appearance: none;
}


button {
  border: 1px solid #333;
  background: none;
  padding: .5em;
  color: #AE6253;
}


ul, ol {
  margin-left: 0;
}

p {
  margin: 0;
}



body {
  font: 62.5%/1 sans-serif;
  background-color: #FBF7E4 ;
}

main {
  max-width: 1000px;
  margin: 0 auto;
  padding: 30px 15px;
}

.main-nav {
  text-align: center;
  margin-bottom: 30px;

  button {
    font-size: 1.5em;
    margin: 0 5px 15px 5px;
    display: inline-block;
    padding: 10px 15px;
    text-transform: uppercase;
    background-color: #b85652;
    border-color: #b85652;
    color: #FFF;
    box-shadow: 0 5px 0px rgba(135, 35, 31, 1);
    letter-spacing: 1px;
    text-shadow: 1px 1px 0 #333;
    border-radius: 15px;
    position: relative;
    transition: all .1s;

    &:active,
    &.active,
    &:focus {
      outline: none;
      background-color: #bde0d7;
      color: #4d4d4d;
      text-shadow: 1px 1px 0 #EFEFEF;
      border-color: #bde0d7;
      box-shadow: 0px 3px 0px rgba(70,122,107,1);
      top: 3px;
    }
  }
}

.main-header {
  border-top: 1px dashed #b85652;
  border-bottom: 1px dashed #b85652;
  padding: 30px 0;
  margin-bottom: 30px;
}

.main-title {
  text-align: center;
  margin: 0 0 0 0;
  font: 700 4em/1 georgia;
  text-transform: uppercase;
  color: #4d4d4d;
}

.wrap-content {
  text-align: center;
}
